@use "base/variables" as v;
@use "base/mixins" as m;

.notavif .notwebp .site-header {
  @include m.bgimage(v.$secundarioOpacity, "../img/header.png", top, center);
}
.avif .site-header {
  @include m.bgimage(v.$secundarioOpacity, "../img/header.avif", top, center);
}
.notavif .webp .site-header {
  @include m.bgimage(v.$secundarioOpacity, "../img/header.webp", top, center);
}
.site-header {
  padding: 5rem 0;
  .logo {
    max-width: 20rem;
    margin: 0 auto;
    img {
      object-fit: contain;
    }
    @include m.tablet {
      margin: 0;
    }
  }
  .contenido-header {
    padding-top: 10rem;
    padding-bottom: 10rem;
    @include m.desktop {
      padding-top: 20rem;
      padding-bottom: 20rem;
    }
    h1 {
      font-size: 3rem;
      color: v.$blanco;
      @include m.tablet {
        font-size: 4.5rem;
        max-width: 60%;
      }
      @include m.desktop {
        font-size: 6rem;
        max-width: 50%;
      }
    }
  }
}
