@use "base/variables" as v;
@use "base/mixins" as m;

.planes {
  background-color: v.$secundario;

  h2 {
    @include m.headingborder(v.$blanco, v.$primario);
  }
  .descripcion {
    color: v.$blanco;
  }
}
.nuestros-planes {
  display: flex;
  flex-direction: column;
  @include m.tablet {
    flex-direction: row;
    max-width: 80rem;
    margin-inline: auto;
    margin-block-start: 10rem;
  }

  .plan {
    flex-grow: 1;
    padding: 16rem 4rem 4rem 4rem;
    border-bottom: 1px solid #e1e1e1;
    background-color: v.$blanco;
    @include m.tablet {
      border: none;
      transition-property: transform;
      &:hover {
        transform: scale(1.1);
      }
    }
    .precio {
      font-size: 1.8rem;
      text-align: center;
      font-weight: 700;
      text-transform: uppercase;
    }
    .listado {
      li {
        text-align: center;
        margin-bottom: 2rem;
        &:last-of-type {
          margin-bottom: 0;
        }
      }
    }
    &.inicio {
      @include m.iconogb("../img/icono_inicio.svg");
    }
    &.negocio {
      background-color: v.$primario;
      @include m.iconogb("../img/icono_negocio.svg");
      order: -1;
      @include m.tablet {
        order: unset;
        margin-block: -5rem;
        border-radius: 2rem;
        background-position: 50% 7rem;
        padding-top: calc(16rem + 5rem);
        z-index: 100;
      }
    }
    &.pro {
      @include m.iconogb("../img/icono_pro.svg");
    }
  }
}
