//Import
@use "base/variables" as v;
@use "base/mixins" as m;

html {
  font-size: 62.5%; /*!Important : de esta forma mantenemos la relacion de Rem*/
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  font-family: v.$fuenteprincipal;
  font-size: 1.6rem;
  background-color: v.$blanco;
  overflow-x: hidden;
}
img {
  max-width: 100%;
  display: block;
}
figure {
  margin: 0;
}
ul,
li {
  list-style: none;
  padding: 0;
  margin: 0;
}
.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}
.seccion {
  padding: 3rem 0;
  @include m.tablet {
    padding: 8rem 0;
  }
}
