@use "base/variables" as v;
@use "base/mixins" as m;

h1,
h2,
h3 {
  font-family: v.$fuentesecundaria;
  margin: 0 0 v.$separacion 0;
  text-transform: uppercase;
  text-align: center;
  line-height: 1.5;
}
h1 {
  font-size: 4rem;
}
h2 {
  font-size: 3.6rem;
  color: v.$secundario;
  margin: v.$separacion 0;
}

h3 {
  font-size: 3.2rem;
}

p {
  color: v.$negro;
  font-size: 1.6rem;
  line-height: 2;
}
a {
  text-decoration: none;
  margin: 0;
  color: v.$negro;
}
