@use "base/variables" as v;

//LLamamaos el media con los valores de pantalla para  telefono
@mixin telefono {
  @media (min-width: v.$telefono) {
    @content;
  }
}
//LLamamaos el media con los valores de pantalla para  tablet
@mixin tablet {
  @media (min-width: v.$tablet) {
    @content;
  }
}
//LLamamaos el media con los valores de pantalla para  desktop
@mixin desktop {
  @media (min-width: v.$desktop) {
    @content;
  }
}

//Mixin de Boton
@mixin boton($colorFondo, $ColorTexto) {
  background-color: $colorFondo;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: $ColorTexto;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;

  &:hover {
    cursor: pointer;
    background-color: darken($colorFondo, 10);
  }

  @include tablet {
    width: auto;
  }
}

//Mixin de Grid
@mixin grid($columnas, $espaciado) {
  display: grid;
  grid-template-columns: repeat($columnas, 1fr);
  gap: $espaciado;
}

//Mixin de im background
@mixin bgimage($ColorDegradado, $Url, $posicion1, $posicion2) {
  background-image: linear-gradient($ColorDegradado 0%, $ColorDegradado 100%),
    url($Url);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: $posicion1 $posicion2;
}
//Mixin de icono bg
@mixin iconogb($Url) {
  background-image: url($Url);
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
}
//Mixin de Titulos con linea abajo
@mixin headingborder($ColorTexto, $ColorLinea) {
  font-size: 3.6rem;
  color: $ColorTexto;
  margin: v.$separacion 0;
  line-height: 1.2;
  display: inline-block;
  @include tablet {
    margin: 0;
  }
  &::after {
    content: "";
    background-color: $ColorLinea;
    height: 1rem;
    width: calc(100% + 1rem);
    display: block;
    margin: -1.4rem 0 0 0;
  }
}
